body {
  margin: 0;
  background-color: #fff;
  height: 100%;
}
.dashboard {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  width: 100%;
  height: 100%;
}
.dashboard .device-menu {
  width: 300px;
  background-image: -webkit-linear-gradient(top, #664a86, #534292);
  background-image: linear-gradient(to bottom, #664a86, #534292);
  background-color: #534292;
}
.dashboard .device-menu header {
  color: #fff;
  font-size: 32px;
  text-align: center;
  padding: 5px 0;
  padding-top: 20px;
  padding-bottom: 10px;
  box-shadow: 0 1px 0 rgba(0,0,0,0.1);
  background-color: rgba(0,0,0,0.05);
}
.dashboard .device-menu .list {
  display: block;
  list-style: none;
  padding: 0;
  margin: 10px 0;
  margin-top: 20px;
}
.dashboard .device-menu .item {
  line-height: 36px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  padding: 5px 10px;
  margin: 10px;
  cursor: pointer;
  color: #fff;
  margin: 0;
  font-size: 18px;
}
.dashboard .device-menu .item:hover {
  background-color: rgba(255,255,255,0.2);
}
.dashboard .device-menu .item.active {
  background-color: rgba(0,0,0,0.1);
}
.dashboard .device-info {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
      -ms-flex: 1;
          flex: 1;
  width: 20%;
  position: relative;
  padding: 20px;
}
.dashboard .device-info textarea {
  font-size: 14px;
  width: 100%;
  height: 50px;
  border: 1px solid #ddd;
}
.dashboard .qrcode-wrap {
  box-sizing: border-box;
  width: 200px;
  padding: 10px;
  taxe-aligin: center;
}
.dashboard .qrcode-wrap .qrcode {
  text-align: center;
  padding: 10px 0;
}
.dashboard .qrcode-wrap .qrcode-tips {
  text-align: center;
  color: #333;
}


